/**
 * Modern CSS Reset Tweaks
 * ==================================================
 * A collection of modern CSS reset and normalization styles
 * to ensure consistent behavior across browsers, OS and devices.
 */
/* Ensure consistent font resizing on mobile devices */
html {
  -webkit-text-size-adjust: 100%;
}
html:focus-within {
  scroll-behavior: smooth;
}

/* Basic body setup for layout and text rendering optimization */
body {
  text-size-adjust: 100%;
  position: relative;
  width: 100%;
  min-height: 100vh;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeSpeed;
}

/* Apply box-sizing globally for consistent element sizing */
*,
::after,
::before {
  box-sizing: border-box;
}

/* Style unclassed links for better accessibility */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/**
 * CSS Reset Tweaks
 * Based on Eric Meyer's CSS Reset v2.0-modified (public domain)
 * URL: http://meyerweb.com/eric/tools/css/reset/
 */
a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
br,
button,
canvas,
caption,
center,
cite,
code,
col,
colgroup,
data,
datalist,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
head,
header,
hgroup,
hr,
html,
i,
iframe,
img,
input,
ins,
kbd,
label,
legend,
li,
link,
main,
map,
mark,
menu,
meta,
meter,
nav,
noscript,
object,
ol,
optgroup,
option,
output,
p,
param,
picture,
pre,
progress,
q,
rb,
rp,
rt,
rtc,
ruby,
s,
samp,
script,
section,
select,
small,
source,
span,
strong,
style,
svg,
sub,
summary,
sup,
table,
tbody,
td,
template,
textarea,
tfoot,
th,
thead,
time,
title,
tr,
track,
tt,
u,
ul,
var,
video,
wbr {
  font-size: 100%;
  font: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* Add focus styles to improve accessibility */
:focus {
  outline: 0;
}

/* Normalize HTML5 elements for older browsers */
article,
aside,
details,
embed,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
object,
section {
  display: block;
}

canvas,
iframe {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Remove default list styling */
ol,
ul {
  list-style: none;
}

/* Normalize quote styling */
blockquote,
q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

/* Reset and normalize form inputs */
input:required,
input {
  box-shadow: none;
}

/* Autofill styling for better compatibility */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset;
}

/* Improve appearance of search inputs */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

input:focus {
  outline: none;
}

video {
  background: #000;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 */
[hidden] {
  display: none;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: none;
}

/**
 * Make media easier to work with
 */
audio,
img,
picture,
svg,
video {
  max-width: 100%;
  display: inline-block;
  vertical-align: middle;
  height: auto;
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
  border: 0;
  background: transparent;
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/* Additional attribute handling for accessibility */
[disabled],
[disabled=true],
[aria-disabled=true] {
  pointer-events: none;
}

/**
 * Address box sizing set to content-box in IE 8/9.
 */
input[type=checkbox],
input[type=radio] {
  padding: 0;
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button {
  border: 0;
  background: transparent;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
  text-indent: 0;
}

/**
 * Based on normalize.css v8.0.1
 * github.com/necolas/normalize.css
 */
hr {
  box-sizing: content-box;
  overflow: visible;
  background: #000;
  border: 0;
  height: 1px;
  line-height: 0;
  margin: 0;
  padding: 0;
  page-break-after: always;
  width: 100%;
}

/**
 * Correct the inheritance and scaling of font size in all browsers.
 */
pre {
  font-family: monospace, monospace;
  font-size: 100%;
}

/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none;
  text-decoration: none;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 75%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -5px;
}

sup {
  top: -5px;
}

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0;
  padding: 0;
}

/**
 * Show the overflow in IE and Edge.
 */
button,
input {
  /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 */
button,
select {
  /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
  outline: 0;
}

legend {
  color: inherit;
  white-space: normal;
  display: block;
  border: 0;
  max-width: 100%;
  width: 100%;
}

fieldset {
  min-width: 0;
}

body:not(:-moz-handler-blocked) fieldset {
  display: block;
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
template {
  display: none;
}

:is(h1, h2, h3) {
  color: blue;
}

h1,
h2,
h3,
h4,
a,
p,
label,
input,
button,
textarea {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
}

body {
  background-color: #fbfbfb;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

header {
  padding: 40px 80px;
  border-radius: 10px;
  box-shadow: 0 0 10px #e8f9ff;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
}
header > section {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
header .intro {
  grid-column: 1/2;
  grid-row: 1/2;
  gap: 50px;
}
header .avatar {
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
  z-index: 1;
}
header .avatar svg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
header figure {
  align-self: center;
}
header figure img {
  border-radius: 100%;
  box-shadow: 0 0 10px #e8f9ff;
}
header h1 {
  font-size: 4em;
  font-weight: bold;
  text-wrap: balance;
}
header h1 > strong {
  color: #000;
}
header a {
  border: 2px solid #c5baff;
  background-color: #c5baff;
  color: #000;
  margin-top: 30px;
  border-radius: 10px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
  text-decoration: none;
  display: inline-block;
  width: fit-content;
  padding: 15px 30px;
}
header a:hover {
  cursor: pointer;
  background-color: #c4d9ff;
  box-shadow: 0 0 10px #c4d9ff;
}

.projects {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 100px 1fr;
  gap: 30px;
}
.projects h2 {
  grid-column: 1/4;
  grid-row: 1/2;
  text-align: center;
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 50px;
}
.projects h2:after {
  content: "";
  display: block;
  position: absolute;
  height: 3px;
  width: 10%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #c5baff;
}
.projects article {
  box-shadow: 0 0 10px #c4d9ff;
  background-color: #e8f9ff;
  border-radius: 10px;
  padding: 25px;
  cursor: pointer;
}
.projects article figure {
  overflow: hidden;
  border-radius: 10px;
}
.projects article img {
  border-radius: 10px;
  transition: transform 0.5s;
}
.projects article img:hover {
  transform: scale(1.1);
}
.projects article h3 {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 1.5em;
  font-weight: bold;
}
.projects article p {
  text-wrap: balance;
}
.projects article a {
  border: 2px solid #c5baff;
  background-color: #c5baff;
  color: #000;
  margin-top: 30px;
  border-radius: 10px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
  text-decoration: none;
  display: inline-block;
  width: fit-content;
  padding: 15px 30px;
}
.projects article a:hover {
  cursor: pointer;
  background-color: #c4d9ff;
  box-shadow: 0 0 10px #c4d9ff;
}

.skills {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 100px 1fr;
  gap: 30px;
  margin-top: 50px;
}
.skills h2 {
  text-align: center;
  font-size: 2em;
  font-weight: bold;
  grid-column: 1/4;
  grid-row: 1/2;
}
.skills h2:after {
  content: "";
  display: block;
  position: absolute;
  height: 3px;
  width: 10%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #c5baff;
}
@keyframes progress-bar {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  50% {
    opacity: 0.5;
  }
  100% {
    transform: translateX(0);
  }
}
.skills .skill {
  overflow: hidden;
  position: relative;
  background: #c4d9ff;
  border-radius: 10px;
  padding: 20px 20px;
  transition: translate 300ms ease-in-out;
}
.skills .skill:hover span span {
  animation: progress-bar 600ms ease-in;
}
.skills .skill h3 {
  font-weight: bold;
  margin-bottom: 10px;
}
.skills .skill:hover {
  box-shadow: 0 0 10px #c5baff;
}
.skills .skill span {
  display: block;
  height: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.skills .skill span span {
  will-change: opacity, transform;
  border-radius: 10px;
  background-color: #c5baff;
}

.contact {
  margin: 50px 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100px 1fr;
}
.contact h2 {
  text-align: center;
  font-size: 2em;
  font-weight: bold;
  grid-column: 1/4;
  grid-row: 1/2;
}
.contact h2:after {
  content: "";
  display: block;
  position: absolute;
  height: 3px;
  width: 10%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #c5baff;
}
.contact form {
  background-color: #e8f9ff;
  padding: 30px 40px;
  border-radius: 10px;
  box-shadow: 0 0 10px #c4d9ff;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.contact form label {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}
.contact form input,
.contact form textarea {
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #e8f9ff;
}
.contact form input:focus-visible,
.contact form textarea:focus-visible {
  outline: none;
  border-color: #c5baff;
}
.contact form button {
  border: 2px solid #c5baff;
  background-color: #c5baff;
  color: #000;
  margin-top: 30px;
  border-radius: 10px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
  display: flex;
  align-self: center;
  justify-content: center;
  gap: 10px;
  padding: 15px 30px;
  width: 100%;
}
.contact form button:hover {
  cursor: pointer;
  background-color: #c4d9ff;
  box-shadow: 0 0 10px #c4d9ff;
}

@media screen and (max-width: 1024px) {
  .container {
    padding: 0 20px;
  }
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
  .projects h2 {
    grid-column: 1/3;
  }
}
@media screen and (max-width: 767px) {
  header {
    padding: 0;
    grid-template-columns: 1fr;
  }
  header a {
    margin: 0;
  }
  header h1 {
    font-size: 2em;
  }
  header .intro,
  header .avatar {
    grid-column: initial;
    grid-row: initial;
  }
  header .intro img,
  header .avatar img {
    width: 70%;
  }
  .projects {
    grid-template-columns: 1fr;
  }
  .projects h2 {
    grid-column: initial;
  }
}